@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Lato", sans-serif;
}

.defaultpage {
  margin-top: 15rem;
  color: grey;
  justify-content: center;
  text-align: center;
}

.navbar {
  background: transparent;
  height: 60px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-size: 1.2rem;
  position: -webkit-sticky;
  position: sticky;
  width: auto;
  top: 0;
  z-index: 999;
  transition: all 0.2s;
}

.nav_white {
  transition: all 0.2s ease-in-out;
  background-color: rgb(255, 255, 255);
  background-blend-mode: overlay;
}

.navbar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60px;
  top: 0;
  position: absolute;
  bottom: 0;
}

.navbar-logo {
  cursor: pointer;
  color: black;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
}

.fa-shopping-cart {
  color: green;
  margin-left: 0.5rem;
  font-size: 1.5rem;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(4, auto);

  list-style: none;
  text-align: center;
  width: 60vw;
  justify-content: end;
  margin-right: 2rem;
}

.nav-item {
  height: 60px;
}

.nav-links {
  display: flex;
  align-items: center;
  text-decoration: none;
  margin-top: 1.2rem;
  padding: 0.1rem 1rem;
  height: auto;
  color: black;
}

.nav-links:hover {
  transform: scale(1.1);
  border-bottom: 0.2rem solid black;
  transition: all 0.2s ease-in-out;
}

.fa-bars {
  color: rgb(0, 0, 0);
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 960px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 40vh;
    position: absolute;
    top: 60px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #242222;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    padding: 1.5rem;
    width: 100%;
    display: table;
    color: white;
  }

  .nav-item:hover {
    background-color: rgb(0, 0, 0);
    color: #2f0000;
    border-radius: 0;
  }

  .navbar-logo {
    position: absolute;
    top: 4px;
    left: 0;
    transform: translate(25%, 50%);
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: -16px;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: rgb(0, 0, 0);
    font-size: 2rem;
  }

  .fa-shopping-cart {
    color: white;
  }
}

:root {
  --primary: #34d955;
}

.btn {
  outline: none;
  border: none;
  cursor: pointer;
  border-radius: 8px;
  font-family: Arial;
  color: #ffffff;
  font-size: 16px;
  padding: 12px;
  text-decoration: none;
}

.btn-main {
  background: #34d955;
  background: var(--primary);
}

.btn-download {
  background: black;
  color: white;
  width: 9rem;
  height: 4rem;
  text-align: left;
}

.btn-main:hover {
  background: #3cfc43;
}

.btn-download:hover {
  background: rgb(68, 68, 68);
}

.btn-mobile {
  margin: 2px;
}

.btn-icon {
  background: #34d955;
  background: var(--primary);
  padding: 5px;
}

@media screen and (max-width: 960px) {
  .btn {
    font-size: 1rem;
    padding: 12px;
  }
}

/* ----------------- cards component ------------- */
.cards {
  height: auto;
  border: none;
}

.cards-container {
  display: flex;
  flex-flow: column;
  width: 100%;
  margin: 0;
  height: auto;
  margin: 1rem 0;
}

/* ------------------ card Item component--------------- */

.card-item {
  margin: 1rem 0;
}
.card {
  margin: 0;
  padding: 0;
  width: 80%;
  border: none;
  border-radius: 15px;
  margin: 0 8%;
}
.card-img {
  height: 22rem;
  border-radius: 15px;
}

/* --------------------- cards info component ------------- */

.card-info-container {
  padding: 30px;
  margin-top: 2.5rem;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.6);
  width: 40%;
  height: 70%;
}

.card-info-container > h4 {
  margin-bottom: 25px;
}

.card-info-container > p {
  display: inline-block;
}

.bottom {
  display: flex;
  background-color: black;
  width: 100%;
  height: 5rem;
  position: absolute;
  top: 80%;
  border-radius: 10px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  margin: 0;
  padding: 0;
  object-fit: contain;
  justify-content: space-between;
  padding: 10px;
}

.bottom > p,
.bottom > h4 {
  display: inline-block;
  margin: 5px;
}

.bottom > p {
  width: 35%;
}

/* -------------name job container--------------- */

.name-job {
  background-color: transparent;
  color: black;
  width: 25%;
  height: 25%;
  position: absolute;
  bottom: 0px;
  right: 30px;
  text-align: right;
}

@media screen and (max-width: 960px) {
  .cards {
    font-size: 1rem;
    margin-bottom: 0;
  }

  .card-info-container {
    width: 80%;
    overflow: scroll;
  }
  .name-job {
    font-size: 12px;
    width: 50%;
    bottom: -40px;
    right: 10px;
  }
  .card-text {
    overflow: none;
  }
  .card-title {
    font-size: 16px;
    font-weight: bold;
  }

  .bottom {
    font-size: 0.8rem;
    height: 32%;
    object-fit: contain;
    box-sizing: border-box;
  }
}

.footer-container {
  background-color: whitesmoke;
  padding: 4.2rem 4.2rem 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.footer-links {
  width: 100%;
  max-width: 1000px;
  display: flex;
  justify-content: center;
}

.company-details-wrap {
  display: flex;
  margin: 5px;
}

.company-logo a {
  color: #34d955;
  font-size: 30px;
  font-weight: bold;
  text-decoration: none;
}

.company-items {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 16px;
  text-align: left;
  width: 160px;
  box-sizing: border-box;
}

.company-items h2 {
  margin-bottom: 16px;
}

.company-items a {
  color: rgb(100, 92, 92);
  text-decoration: none;
  margin-bottom: 10px;
  font-size: 20px;
}

.company-items a:hover {
  color: rgb(110, 110, 110);
  transition: 0.3s ease-out;
}

.social-media-icons {
  font-size: 18px;
  display: flex;
  flex-direction: row;
  width: 100px;
  justify-content: space-between;
}

.social-details-wrap {
  max-width: 1000px;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.fab {
  color: white;
  margin: 5px;
}

.fa-1 {
  cursor: pointer;
  float: left;
  margin-right: 10px;
}

.btn-download > h6 {
  font-size: 10px;
}

.border {
  display: flex;
  opacity: 0;
  justify-content: center;
}

.rights-section {
  background-color: whitesmoke;
  border-top: 2px solid rgb(200, 196, 196);
}
.copy-rights {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 1rem 4rem 0;
}

.reserved-rights {
  display: flex;
  flex-direction: row;
}

.reserved-rights > p {
  margin-right: 1rem;
}

@media screen and (max-width: 1110px) {
  .footer-container {
    padding: 0;
    margin-top: 3rem;
    flex-direction: column;
  }
  .social-details-wrap {
    margin-top: 1rem;
    text-align: center;
    align-items: center;
  }
  .company-description {
    padding: 10px;
    text-align: center;
    width: 80%;
  }

  .social-media-icons {
    width: auto;
    justify-content: center;
  }

  .company-details-wrap {
    flex-direction: column;
  }

  .footer-links {
    justify-content: center;
  }

  .company-items h2 {
    font-size: 2rem;
  }

  .company-items a {
    font-size: 1rem;
  }

  .social-details-wrap {
    flex-direction: column;
  }

  .copy-rights {
    text-align: center;
    flex-direction: column;
  }
  .reserved-rights {
    flex-direction: column;
  }
}

@media screen and (max-width: 960px) {
  .footer-links {
    flex-direction: column;
    justify-content: center;
  }

  .company-details-wrap {
    align-items: center;
  }
  .company-items {
    align-items: center;
  }
}

.header-container {
  background: url("https://i.ibb.co/mCG88tr/freddy-castro-u3aj-SXh-ZM-U-unsplash.jpg")
    center center/cover no-repeat;

  width: 100%;
  height: 80vh;
  z-index: -1;
  display: flex;
  align-items: center;
  justify-content: center;
}
.content {
  margin-top: -150px;
  width: 60%;
  align-items: center;
  justify-content: center;
}
.header-container h1 {
  color: white;
  font-size: 3rem;
  margin-left: 7%;
}
.heading {
  margin-left: 7%;
}
.space-div {
  margin-left: 25%;
}

@media screen and (max-width: 1200px) {
  .content {
    width: 95%;
  }
  .header-container h1 {
    font-size: 2rem;
  }
  .heading {
    margin-left: 0%;
  }
  .space-div {
    margin-left: 0;
  }
}

.search-container {
  margin: 2rem 0;
}
.search-bar {
  width: 100%;
  height: 3rem;
  border: none;
  border-radius: 8px;
  padding-left: 10px;
  font-size: 1rem;
  color: black;
  outline: none;
}

.search-footer-container {
  display: flex;
  justify-content: space-around;
  width: 100%;
  margin-top: 2rem;
}

.radio-container {
  color: rgb(2, 0, 0);
  justify-content: space-around;
}

.radio-container > input {
  margin-left: 15px;
}

.advanced-search-link {
  color: white;
}

@media screen and (max-width: 960px) {
  .search-footer-container {
    flex-direction: column;
    text-align: center;
  }
}

